import Component from "vue-class-component";
import axios from "axios";
import moment from "moment";
import * as indexApi from "../../api/indexApi";
import * as doctorApi from "../../api/doctorApi";

import  updatedoctor from "../doctor/updatedoctor";

@Component({
  props: {},
  components: {
    updatedoctor
    // categoryView,
  }
})
export default class AddGoods extends Vue {

        handleEdit(index, row,state) {

            (<any>this.$refs.updatedoctor).handleEdit(index, row,state)
        }

  hospitallist=[];
    gethospitalList(){
        doctorApi.queryHospitalList().then(res => {
      if (res["retCode"]) {
        this.hospitallist = res.data.hosipitalList;
      } else {
        this.$alert(res["message"]);
        console.error("数据查询错误");
      }
    });
    }
    tableTree=[];
    getdepartmenttree(){
            doctorApi.querytree().then(res => {
      if (res["retCode"]) {
        this.tableTree = res.data.children;
      } else {
        this.$alert(res["message"]);
        console.error("数据查询错误");
      }
    });
        }



  loading = false;


  page = 0;
  pageSize = 10;
  total = 0;
  onPageChange(page) {
    this.page = page - 1;
    this.getDoctorList();
  }
keyword=""
phone=""
DoctorInfo = [];

  startDate = "";
  endDate = "";
  doctorStatus = ""
getDoctorList(filter = null){
  this.loading = true;

  if(filter){
    this.page = 0
  }

    let startCreatTime = "";
    let endCreatTime = "";
    if ((this.startDate || "") != "") {
      startCreatTime =
        moment(this.startDate).format("YYYY-MM-DD")  + " 00:00:00";
    }
    if ((this.endDate || "") != "") {
      endCreatTime = moment(this.endDate).format("YYYY-MM-DD")  + " 23:59:59";
    }
    indexApi.getDoctorList({
        page:this.page,
        pageSize:this.pageSize,
        keyword:this.keyword,
        phone:this.phone,
        startcreateDate:startCreatTime,
        endcreateDate:endCreatTime,
        doctorStatus:this.doctorStatus
    }).then(res => {
      this.loading = false;
      if (res["retCode"]) {
          console.log(res.data)
          this.DoctorInfo = res.data.DoctorInfo
this.total = res.data.page.total;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
}

/**
 * 医生数量
 */
countDoctor = 0
getcountDoctor(){
  indexApi
      .countDoctor({
      })
      .then(res => {
        if (res["retCode"]) {

this.countDoctor = res.data.num
} else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
}


/**
 * 医生查药品
 */


doctorObj =  {
  model: false,
  loading: false,
 YdkDrugList : [],
  page : 0,
  pageSize : 10,
  total : 0,
  onPageChange : page => {
    this.doctorObj.page = page - 1;
    this.doctorGetDoctor(this.doctorObj.row);
  },
  row : {}
  }


doctorGetDoctor(row){
  this.doctorObj.loading= true   
   this.doctorObj.model = true;
   this.doctorObj.row = row;

 indexApi
      .doctorGetDoctor({
docterId:row.doctorId
      })
      .then(res => {
          this.doctorObj.loading= false
        if (res["retCode"]) {
          console.log(res.data)
          this.doctorObj.YdkDrugList = res.data.YdkDrugList
          this.doctorObj.total = res.data.page.total
          } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });

}


/**
 * 医生查顾问
 */


/**
 * 
 * 查看医生
 * 
 */

doctorAdviserObj =  {
  model: false,
  loading: false,
 AdviserInfo : [],
  page : 0,
  pageSize : 10,
  total : 0,
  onPageChange : page => {
    this.doctorAdviserObj.page = page - 1;
    this.doctorGetAdviser(this.doctorAdviserObj.row);
  },
  row : {}
  }


doctorGetAdviser(row){
  this.doctorAdviserObj.loading= true   
   this.doctorAdviserObj.model = true;
   this.doctorAdviserObj.row = row;

 indexApi
      .doctorGetAdviser({
            doctor_id:  row.doctorId,
              page:this.doctorAdviserObj.page,
              pageSize:this.doctorAdviserObj.pageSize
      })
      .then(res => {
  this.doctorAdviserObj.loading= false
        if (res["retCode"]) {
  console.log(res.data)
this.doctorAdviserObj.AdviserInfo = res.data.AdviserInfo
this.doctorAdviserObj.total = res.data.page.total
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
}


/**
 * 停用启用
 */
doSubmitStatus(index,row){
    if(row.doctorStatus =='USE'){
this.handleSTOP(index,row)
    }
      if(row.doctorStatus =='STOP'){
this.handleUSE(index,row)
    }
}       



  handleSTOP(index,row){
      this.$confirm('是否停用该医生?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            doctorApi.setDoctorSTOPstatus(row.doctorId).then(res => {
            if (res["retCode"]) {
this.getDoctorList();
                this.$message({
                    type: 'success',
                    message: '停用成功!'
                });
            } else {
                if(!res['islogin']){this.$alert(res["message"]);}
                console.error("数据查询错误");
            }
            });
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消停用'
          });          
        });
      }

      handleUSE(index,row){
      this.$confirm('是否启用该医生?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            doctorApi.setDoctorUSEstatus(row.doctorId).then(res => {
            if (res["retCode"]) {
       this.getDoctorList();
                this.$message({
                    type: 'success',
                    message: '启用成功!'
                });
            } else {
                if(!res['islogin']){this.$alert(res["message"]);}
                console.error("数据查询错误");
            }
            });
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消启用'
          });          
        });
      }



  mounted() {
this.getDoctorList();
this.getcountDoctor()
this.getdepartmenttree()
this.gethospitalList()
  }
}



